<template>
  <el-container direction="vertical">
    <el-main>
    <Room v-for="room in rooms" :key="room.id" :id="room.id" :name="room.name"></Room>
    </el-main>
    <el-button type="primary">
      创建房间
    </el-button>
  </el-container>
</template>

<script>
import Room from './components/Room.vue'
// import Qs from 'qs'

export default {
  name: 'App',
  components: {
    Room
  },
  data(){
    return {
      rooms:[]
    }
  },
  methods:{
    getRooms:function(){
      this.$axios.get("/rooms").then((response)=>{
        this.rooms = response.data;
      })  
    }
  },
  mounted:function(){
    this.getRooms();
  },
}

</script>

